.ViewStart__h1 {
    margin: 0px auto;
    padding: 20px 0;
    text-align: center;
    font-size: 16px;
    background-color: rgb(166, 194, 226);
}

.ViewStart__h2 {
background-color: rgb(166, 194, 226);
font-size: 14px;
width: 100%;
height: 50px;
padding-top: 20px;
text-align: center;
}

.ViewStart_city {
    font-size: 20px;
    font-weight: 700;
    text-align: center;
    height: 50px;
    padding-top: 20px;
    border-top: 1px solid rgb(67, 49, 171);
    background-color: #1B4778;
}

.ViewStart__temp {
    font-weight: 600;
    height: 50px;
    text-align: center;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 50px;
    padding-top: 20px;
    border: 1px solid rgb(67, 49, 171);
    background-color: #1B4778;


}
.ViewStart__pogoda {
    background-color: #1B4778;
}

.ViewStart__img1 {
    padding: 30px 0px;
    width: 50px;
    height: 50px;
}

.ViewStart__img {
    
    border: 2px solid rgb(66, 124, 191);
    background-color: rgb(27, 71, 120);
    text-align: center;
    p{
        margin-bottom: 10px;
    }
}

.ViewStart__imgAlert {
    background-color: rgb(245, 10, 10);
}

.ViewStart__img > p {
    font-weight: 800;
}

.viewStart_sniezka {
    opacity: 0;
    right: 50px;
    position: absolute;
    width: 30px;
    height: 30px;
}
.viewStart_sniezka1 {
    opacity: 0;
    right: 50px;
    position: absolute;
    width: 30px;
    height: 30px;
}

@media(min-width:500px) {
    .viewStart_sniezka {
        opacity: 1;
        left: 50px;
        position: absolute;
        width: 30px;
        height: 30px;
    }
    .viewStart_sniezka1 {
        opacity: 1;
        right: 50px;
        position: absolute;
        width: 30px;
        height: 30px;
    }
}